.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;
}

.card {
  margin: 0 2% !important;
}

.infected {
  border-bottom: 10px solid #6d6df680;
}

.recovered{
  border-bottom: 10px solid #95e795ed;
}

.deaths{
  border-bottom: 10px solid #f08383;
}

@media only screen and (max-width: 770px) {
  .card {
    margin: 2% 0 !important;
  }
}